import React, { Component } from 'react'

export default class index extends Component {
  constructor(props){
    super(props)
    this.state = {
      pokemon: [],
    }
  }
  
  getPokemon = () => {
    fetch("https://pokeapi.co/api/v2/pokemon/1/")
    .then(res => res.json())
    .then( (result) => {
        this.setState({
          pokemon: result
        });
      })
  }
  render() {
    console.log(this.state.pokemon)
    return (
      <div className="main">
        <div className="pokedex">
          <div className="left"></div>
          <div className="center"></div>
          <div className="right"></div>
        </div>
        <style jsx>{`
          .main{
            display: flex;
            align-items: center;
            justify-content: center;
            height: 100vh;
            width: 100vw;
          }
          .pokedex{
            height: 80vh;
            width: 60vw;
            display: flex;
            align-items: flex-end;
            justify-content: center;
          }
          .pokedex .left{
            height: 100%;
            width: 48%;
            background: #e84848;
          }
          .pokedex .center{
            height: 80%;
            width: 6%;
            background: #e84848;
          }
          .pokedex .right{
            height: 80%;
            width: 48%;
            background: #e84848;
          }
        `}</style>
      </div>
    )
  }
}
